import { Project, User, Position, Allocation } from './types'
import { getSystemUsers, setSystemUsers, getCurrentUserData, setCurrentUserData } from './storage'

// Helper functions
const MONTHS = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']
const YEARS = [2024, 2025, 2026, 2027]
const DEPARTMENTS = ['Engineering', 'Design', 'Marketing', 'Sales', 'HR', 'Finance', 'Operations', 'Product', 'Legal', 'QA']
const PROJECT_NAMES = [
  'Website Redesign', 'Mobile App Development', 'Cloud Migration', 'AI Integration',
  'Database Optimization', 'Security Audit', 'API Development', 'UI/UX Overhaul',
  'Performance Testing', 'DevOps Implementation', 'Analytics Platform', 'Customer Portal',
  'Payment System', 'Inventory Management', 'Reporting Dashboard', 'Email Campaign',
  'Social Media Integration', 'Search Engine Optimization', 'Content Management', 'E-commerce Platform'
]

const POSITION_NAMES = [
  'Project Manager', 'Senior Developer', 'Junior Developer', 'Frontend Developer',
  'Backend Developer', 'Full Stack Developer', 'DevOps Engineer', 'QA Engineer',
  'UI/UX Designer', 'Product Designer', 'Business Analyst', 'Data Analyst',
  'Marketing Manager', 'Sales Representative', 'Customer Support', 'System Administrator',
  'Database Administrator', 'Security Specialist', 'Technical Writer', 'Scrum Master'
]

function randomChoice<T>(array: T[]): T {
  return array[Math.floor(Math.random() * array.length)]
}

function randomRange(min: number, max: number): number {
  return Math.floor(Math.random() * (max - min + 1)) + min
}

function randomDate(startYear: number, endYear: number): { month: number; year: number } {
  const year = randomChoice(YEARS.filter(y => y >= startYear && y <= endYear))
  const month = randomRange(0, 11)
  return { month, year }
}

// Step 1: Clear all data
export function clearAllData() {
  console.log('üóëÔ∏è  Clearing all existing data...')
  
  // Clear current user data
  const currentUser = getCurrentUserData()
  setCurrentUserData({
    projects: [],
    users: [],
    allocations: [],
    positions: []
  })
  
  // Clear system users
  setSystemUsers([])
  
  console.log('‚úÖ All data cleared')
}

// Step 2: Create 10 projects with ~20 positions each
export function createProjects(): { projects: Project[], positions: Position[] } {
  console.log('üèóÔ∏è  Creating 10 projects with positions...')
  
  const projects: Project[] = []
  const positions: Position[] = []
  const usedProjectNames = new Set<string>()
  
  for (let i = 0; i < 10; i++) {
    // Pick unique project name
    let projectName
    do {
      projectName = randomChoice(PROJECT_NAMES)
    } while (usedProjectNames.has(projectName))
    usedProjectNames.add(projectName)
    
    // Random start and end dates
    const startDate = randomDate(2024, 2025)
    const endDate = randomDate(startDate.year, startDate.year + 2)
    
    // Ensure end date is after start date
    const startGlobalIdx = startDate.year * 12 + startDate.month
    const endGlobalIdx = endDate.year * 12 + endDate.month
    const finalEndIdx = Math.max(startGlobalIdx + 3, endGlobalIdx) // At least 3 months duration
    
    const finalEndDate = {
      year: Math.floor(finalEndIdx / 12),
      month: finalEndIdx % 12
    }
    
    const project: Project = {
      id: `proj-${Date.now()}-${i}`,
      name: `${projectName} ${i + 1}`,
      color: randomChoice([
        '#3B82F6', '#10B981', '#F59E0B', '#8B5CF6', '#EC4899',
        '#06B6D4', '#EF4444', '#14B8A6', '#F97316', '#84CC16'
      ]),
      startMonth: startDate.month,
      startYear: startDate.year,
      endMonth: finalEndDate.month,
      endYear: finalEndDate.year,
      allocationMode: randomChoice(['percentage', 'days'])
    }
    
    projects.push(project)
    
    // Create ~20 positions for this project
    const positionCount = randomRange(18, 22)
    const usedPositionNames = new Set<string>()
    
    for (let j = 0; j < positionCount; j++) {
      let positionName
      do {
        positionName = randomChoice(POSITION_NAMES)
      } while (usedPositionNames.has(positionName))
      usedPositionNames.add(positionName)
      
      // Create positions for each month in the project timeline
      for (let monthIdx = startGlobalIdx; monthIdx <= finalEndIdx; monthIdx++) {
        const position: Position = {
          id: `pos-${project.id}-${j}-${monthIdx}`,
          projectId: project.id,
          monthIndex: monthIdx,
          percentage: randomRange(50, 100), // Random allocation percentage
          allocated: 0,
          name: positionName,
          days: project.allocationMode === 'days' ? randomRange(10, 20) : undefined
        }
        positions.push(position)
      }
    }
  }
  
  console.log(`‚úÖ Created ${projects.length} projects with ${positions.length} total positions`)
  return { projects, positions }
}

// Step 3: Create 100 staff members
export function createStaff(): User[] {
  console.log('üë• Creating 100 staff members...')
  
  const firstNames = ['John', 'Jane', 'Mike', 'Sarah', 'David', 'Emily', 'Chris', 'Lisa', 'Tom', 'Anna', 'Robert', 'Maria', 'James', 'Jennifer', 'William', 'Linda', 'Richard', 'Patricia', 'Charles', 'Barbara']
  const lastNames = ['Smith', 'Johnson', 'Williams', 'Brown', 'Jones', 'Garcia', 'Miller', 'Davis', 'Rodriguez', 'Martinez', 'Wilson', 'Anderson', 'Taylor', 'Thomas', 'Hernandez', 'Moore', 'Martin', 'Jackson', 'Thompson', 'White']
  
  const users: User[] = []
  const usedNames = new Set<string>()
  
  for (let i = 0; i < 100; i++) {
    let firstName, lastName, fullName
    do {
      firstName = randomChoice(firstNames)
      lastName = randomChoice(lastNames)
      fullName = `${firstName} ${lastName}`
    } while (usedNames.has(fullName))
    usedNames.add(fullName)
    
    const startDate = randomDate(2024, 2025)
    const endDate = Math.random() > 0.8 ? randomDate(startDate.year, startDate.year + 2) : undefined
    
    const user: User = {
      id: `user-${Date.now()}-${i}`,
      name: fullName,
      department: randomChoice(DEPARTMENTS),
      startDate: `${startDate.year}-${String(startDate.month + 1).padStart(2, '0')}-01`,
      endDate: endDate ? `${endDate.year}-${String(endDate.month + 1).padStart(2, '0')}-01` : undefined,
      workDays: randomChoice(['mon-fri', 'sun-thu']),
      role: randomChoice(['admin', 'editor', 'viewer']),
      email: `${firstName.toLowerCase()}.${lastName.toLowerCase()}@company.com`,
      isActive: true
    }
    
    users.push(user)
  }
  
  console.log(`‚úÖ Created ${users.length} staff members`)
  return users
}

// Step 4: Allocate positions to staff randomly
export function createAllocations(users: User[], projects: Project[], positions: Position[]): Allocation[] {
  console.log('üéØ Allocating positions to staff...')
  
  const allocations: Allocation[] = []
  const availablePositions = positions.filter(p => p.percentage > 0)
  
  // Shuffle users and positions for random distribution
  const shuffledUsers = [...users].sort(() => Math.random() - 0.5)
  const shuffledPositions = [...availablePositions].sort(() => Math.random() - 0.5)
  
  // Allocate roughly 60-80% of positions
  const positionsToAllocate = Math.floor(shuffledPositions.length * randomRange(0.6, 0.8))
  
  for (let i = 0; i < positionsToAllocate; i++) {
    const position = shuffledPositions[i]
    const user = shuffledUsers[i % shuffledUsers.length]
    
    // Random allocation percentage (can be less than or equal to position requirement)
    const maxAllocation = position.percentage
    const allocationPercentage = randomRange(Math.floor(maxAllocation * 0.5), maxAllocation)
    
    const allocation: Allocation = {
      id: `alloc-${Date.now()}-${i}`,
      userId: user.id,
      projectId: position.projectId,
      monthIndex: position.monthIndex,
      percentage: allocationPercentage,
      positionId: position.id,
      positionName: position.name
    }
    
    allocations.push(allocation)
  }
  
  console.log(`‚úÖ Created ${allocations.length} allocations`)
  return allocations
}

// Master function to execute all steps
export function generateTestData() {
  console.log('üöÄ Starting test data generation...')
  
  // Step 1: Clear all data
  clearAllData()
  
  // Step 2: Create projects and positions
  const { projects, positions } = createProjects()
  
  // Step 3: Create staff
  const users = createStaff()
  
  // Step 4: Create allocations
  const allocations = createAllocations(users, projects, positions)
  
  // Save all data
  setCurrentUserData({
    projects,
    users,
    positions,
    allocations
  })
  
  console.log('üéâ Test data generation complete!')
  console.log(`üìä Summary: ${projects.length} projects, ${users.length} users, ${positions.length} positions, ${allocations.length} allocations`)
  
  return { projects, users, positions, allocations }
}
